  import {useState} from 'react'

  export default function MenuButton ({open, color, onClick}) {
    const [openButton, setOpenButton] = useState(open)
    const [colorButton] = useState(color ? color : 'black' )
    //const componentWillReceiveProps = open => {
      //  open !== openButton ? open : null
    //}
    const handleClick = () => {
        setOpenButton(!openButton)
    }
    console.log('lhttt')
    return (
        <div>
           <div className="button" onClick = {onClick ? onClick : handleClick}>
                <div className ="line top"></div>       
                <div className="line middle"/>
                <div className="line bottom"/>
            </div> 
           <style jsx>
            {`
                .button {
                    height: 35px;
                    width: 35px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                    padding: 4px;
                    }
                .line {
                    height: 2px;
                    width: 20px;
                    background-color: ${colorButton}; 
                    transition: all 0.2s ease;
                }
                .top {
                    transform: ${openButton ? 'rotate(45deg)' : 'none'};
                    transform-origin: top left;
                    margin-bottom: 5px;
                }
                .middle {
                    opacity: ${openButton ? 0 : 1};
                    transform: ${openButton ? 'translateX(-16' : 'none'};
                }
                .bottom {
                    transform: ${openButton ? 'translateX(-1px) rotate(-45deg)' : 'none'};
                    transform-origin: top left;
                    margin-top: 5px;
                }
                `}
            </style>
        </div>
    )
  }